



















import { Component, Vue } from 'vue-property-decorator';

@Component
export default class NewMovies extends Vue {
    authors = [];
    query = {
        title : "",
        author_id : ""
    };
    notice = "";
    error = {};
    
    created() {
        let that = this
        let callback = ()=>{
            that.axios.get("/api/v1/authors.json", {withCredentials: true}).then((response)=>{
                that.authors = response.data;
            });
        }
        this.$root.$on("added-author", callback);
        callback();
    }

    newMovie() {
        this.error = {};
        this.notice = "";
        this.axios.post("/api/v1/movies.json", this.query, {withCredentials: true}).then((response)=>{
            this.notice = "Successfully created movie " + this.query.title + "!"
        }).catch((reason)=>{
            this.error = reason.response.data;
        })
    }
}
